.App {
  padding-left: calc(100vw - 100%);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans,
    Helvetica Neue, sans-serif;
  min-height: 100vh;
  position: relative;
}
.flex {
  display: flex;
}
.aside {
  flex: 0 0 270px;
  max-width: 270px;
  min-width: 270px;
  width: 270px;
  border-right: 0.5px solid #00223f;
  position: fixed;
  min-height: 100vh;
}
.main {
  padding: 12px 36px;
  margin-left: 270px;
}
.light .aside {
  border-right: 0.5px solid #eee;
}
.main {
  display: flex;
  flex: auto;
  flex-direction: column;
  min-height: 0;
}
.ui {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  max-width: 1500px;
  min-height: 100vh;
  margin: 0 auto;
  overflow: hidden;
}
.ui:after,
.ui:before {
  content: "";
  pointer-events: none;
  display: block;
  position: absolute;
  background: radial-gradient(circle, #331f57 0, rgba(43, 27, 70, 0) 50%);
  width: 1050px;
  height: 1050px;
  top: -500px;
  opacity: 0.6;
  z-index: -1;
}
.ui.light:after,
.ui.light:before {
  display: none;
}
.ui:before {
  left: -500px;
}
.ui:after {
  right: -500px;
}
.circle {
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  background-color: #fff;
  top: -10px;
  left: -10px;
  z-index: 0;
}
.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 24px 12px;
}
.logo {
  padding: 48px 28px 12px;
}
.logo a,
.logo a:active,
.logo a:hover {
  font-size: 30px;
  font-weight: 800;
  color: #fff;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  -moz-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
}
.text-blue {
  color: #387ff3 !important;
}
.light .logo a {
  background: linear-gradient(90deg, rgba(24, 216, 169, 1) 0%, rgba(57, 128, 234, 1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-filter: drop-shadow(3px 3px #ddd);
  filter: drop-shadow(3px 3px #ddd);
  animation: none;
}
@keyframes glow {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px rgba(57, 128, 234, 1);
  }
  to {
    text-shadow: 0 0 20px #fff, 0 0 30px rgba(24, 216, 169, 1), 0 0 40px rgba(24, 216, 169, 1);
  }
}
.address {
  border-radius: 30px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 8px 12px;
  padding-left: 28px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 250px;
  position: relative;
}
.light .address {
  border: 2px solid #eee;
}
.address:before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 12px;
  height: 8px;
  width: 8px;
  border-radius: 8px;
  background-color: #18d8a9;
}
.menu {
  padding: 8px 23px;
  display: flex;
  flex-direction: column;
}
.menu-item {
  padding: 7px 12px 7px;
  margin-bottom: 8px;
  border-radius: 4px;
  font-size: 16px;
  color: #ccc;
  font-weight: 600;
  transition: all 0.3s;
}
.light .menu-item {
  color: rgb(16, 16, 16);
}
.menu-item:hover {
  color: #25b8c0;
}
.light .menu-active,
.menu-active,
.menu-active:hover {
  background: rgb(24, 216, 169);
  background: linear-gradient(90deg, rgba(24, 216, 169, 1) 0%, rgba(57, 128, 234, 1) 100%);
  color: #fff;
}
.card {
  overflow: hidden;
  border-radius: 6px;
  transition: all 0.3s;
  padding: 12px 12px 0px 12px;
  box-shadow: 2px 2px 14px rgba(0, 0, 0, 0.15);
}
.card.animation:hover {
  cursor: pointer;
  transform: translateY(-3%);
}
.dark .card {
  background-color: #1a162e;
}
.card .card-info {
  padding: 12px;
}
.text-center {
  text-align: center;
}
h3 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 0;
}

.image-wrapper img {
  border-radius: 6px;
  aspect-ratio: 1 / 1;
  width: 100%;
  object-fit: cover;
}
.img-card {
  display: flex;
  align-items: center;
  flex-basis: 25%;
}
.nowrap {
  white-space: nowrap;
}
.list {
  /* display: flex;
  align-items: center; */
  margin-bottom: 12px;
}
.list h3 {
  margin-bottom: 4px;
}
.status {
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  width: fit-content;
  border: 2px solid rgba(57, 128, 234, 1);
}

.status.green {
  border: 2px solid rgba(24, 216, 169, 1);
}
.status.red {
  border: 2px solid rgb(240, 38, 92);
}
.nft-detail-img {
  width: 50%;
}
.btn {
  padding: 6px 12px;
  border-radius: 30px;
  width: fit-content;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
}
.btn-primary {
  border: none;
  color: #fff;
  background: linear-gradient(90deg, rgba(24, 216, 169, 1) 0%, rgba(57, 128, 234, 1) 100%);
}
.btn-secondary {
  background-color: transparent;
  border: 2px solid #387ff3;
}
.btn-secondary.green {
  border: 2px solid #18d8a9 !important;
}
.w-20 {
  width: 20%;
}
.w-30 {
  width: 30%;
}
.title {
  font-size: 28px;
}
.ant-btn {
  height: 40px !important;
}
.ant-btn.ant-btn-primary {
  font-size: 16px !important;
  font-weight: 600 !important;
  border: none !important;
  background: linear-gradient(90deg, rgba(24, 216, 169, 1) 0%, rgba(57, 128, 234, 1) 100%) !important;
}
.ant-btn.ant-btn-primary:hover {
  background: linear-gradient(90deg, rgba(24, 216, 169, 1) 0%, rgba(57, 128, 234, 1) 100%) !important;
  border: none !important;
}
